    input[type='radio'] {
        -webkit-appearance: radio;
        -moz-appearance: radio;
        appearance: radio;
    }

    input[type='checkbox'] {
        -webkit-appearance: checkbox;
        -moz-appearance: checkbox;
        appearance: checkbox;
    }

    /* IE10 이상에서 input box 에 추가된 지우기 버튼 제거 */
    input[type='text']::-ms-clear {
        display: none;
    }
    /* input type number 에서 화살표 제거 */
    input[type='number']::-webkit-inner-spin-button,
    input[type='number']::-webkit-outer-spin-button {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
    }

    input[type='number'] {
        -moz-appearance: textfield;
    }

    input[type='submit'] {
        cursor: pointer;
    }

    /* Select box 스타일 초기화 */
    select {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
    }

    /* IE 에서 Select box 화살표 제거 */
    select::-ms-expand {
        display: none;
    }

    button {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        border: 0;
        cursor: pointer;
        font-size: 13px;
        font-size: 14px;

        @media (min-width: 768px) {
            font-size: 15px;
        }
    }
}
