header#header {
    background-color: #1f8ce6;

    .container {
        max-width: 1000px;
        margin: 0 auto;
    }

    nav {
        display: flex;
        flex-wrap: wrap;
        padding: 9px 10px;

        @media (min-width: 999px) {
            padding: 16px 24px;
        }
    }

    a.logo {
        order: 1;
        display: flex;
        align-items: center;

        div {
            width: 50px;

            @media (min-width: 768px) {
                width: 60px;
            }

            svg {
                width: 100%;
                fill: white;
            }
        }

        span {
            display: inline-block;
            position: relative;
            color: white;
            opacity: 0.6;
            padding: 12px;
            margin-top: -2px;
            font-size: 12px;

            &::before {
                content: '';
                display: inline-block;
                height: 30%;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-40%);
                border-left: 1px solid white;
                margin-left: 6px;
                // padding-left: 5px;
            }
        }

        // img {
        //     width: 88px;

        //     @media (min-width: 768px) {
        //         width: 150px;
        //     }
        // }
    }

    .search {
        order: 3;
        margin-top: 10px;
        width: 100%;

        @media (min-width: 768px) {
            margin-top: 0;
            margin-left: 20px;
            order: 2;
            max-width: 340px;
        }

        .box {
            display: flex;
            align-items: center;
            width: 100%;
            height: 36px;
            line-height: 36px;
            min-width: unset;
            background-color: #ffffff;
            border-radius: 3px;

            @media (min-width: 768px) {
            }
        }

        img {
            order: 1;
            margin: 0 7px;
            opacity: 0.3;
            width: 20px;
            height: 20px;
            transition: 0.3s;

            @media (min-width: 768px) {
                margin: 0 10px;
            }
        }

        input {
            border: 0;
            order: 2;
            flex: 1;
            padding-right: 7.75px;
            font-size: 14.5px;
            margin-bottom: -1px;

            @media (min-width: 768px) {
                font-size: 15.5px;
                margin-bottom: -2px;
            }
        }

        input:focus + img {
            opacity: 1;
        }
    }

    .sign-state {
        order: 2;
        margin-left: auto;

        @media (min-width: 768px) {
            order: 3;
        }

        button {
            height: 30px;
            line-height: 30px;
            padding: 0 8px;
            border-radius: 3px;
            font-weight: bold;
            word-break: keep-all;
            color: #1f8ce6;
            border: 1px solid white;

            @media (min-width: 768px) {
                padding: 0 16px;
            }

            &.line--btn {
                background-color: #1f8ce6;
                color: white;
                border: 1px solid #99d1ff;
            }
        }
    }

    ul {
        display: flex;

        @media (min-width: 768px) {
            padding: 0 20px;
        }

        li {
            position: relative;
            width: 25%;
            height: 40px;
            display: flex;
            justify-content: center;
            opacity: 0.5;

            @media (min-width: 768px) {
                width: auto;
                height: 37px;
                margin-right: 50px;

                &:last-child {
                    margin-right: 0;
                }
            }

            &:hover {
                opacity: 0.8;
            }

            &.active {
                opacity: 1;

                .line {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    border-top: 3px solid white;
                }
            }
        }

        button {
            background-color: #1f8ce6;
            display: flex;
            align-items: center;

            span {
                display: none;

                @media (min-width: 768px) {
                    display: block;
                    margin-left: 15px;
                    font-size: 16px;
                    color: white;
                }
            }
        }
    }
}
