article.category-article {
    padding: 36px 0 30px;

    .container {
        margin: 0 auto;

        @media (min-width: 1024px) {
            max-width: 1000px;
        }
    }

    h2 {
        padding-left: 16px;
        margin-bottom: 30px;
        font-size: 19px;
        line-height: 19px;

        @media (min-width: 1024px) {
            max-width: 1000px;
            padding-left: 24px;
            margin: 0 auto 30px auto;
        }

        a {
            &::after {
                content: '〉';
                display: inline-block;
                padding-left: 8px;
                transform: scale(0.85);
            }
        }
    }

    ul {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        overflow-x: scroll;
        height: 364px;
        padding-left: 16px;

        @media (min-width: 768px) {
            height: 414px;
            padding-left: 24px;
        }

        @media (min-width: 1024px) {
            overflow: initial;
        }
    }

    li {
        width: 308px;
        height: 120px;
        margin-right: 20px;
        padding-right: 16px;

        @media (min-width: 768px) {
            height: 138px;
        }
    }

    .flex {
        align-items: stretch;
    }

    img {
        width: 100px;
        height: 100px;
        object-fit: cover;
        margin-right: 20px;
        border-radius: 6px;

        @media (min-width: 768px) {
            width: 120px;
            height: 120px;
        }
    }

    .information {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100px;
        line-height: 1.5;
        font-size: 14px;
        padding-left: 20px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);

        @media (min-width: 768px) {
            height: 120px;
        }
    }

    b {
        width: 132px;
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: bold;
        margin-bottom: 7px;

        @media (min-width: 768px) {
            font-size: 15px;
        }
    }

    .name {
        font-size: 0.9em;
        opacity: 0.9;
    }

    p {
        opacity: 0.7;
        display: inline-block;
        padding-left: 10px;
    }

    span {
        margin-left: 3px;
    }
}
