section#order-complete,
section#order,
section#basket {
    .container {
        max-width: 1000px;
        margin: 0 auto;

        @media (min-width: 1024px) {
            padding: 24px 13px;
        }
    }

    .order-wrapper,
    .cart-wrapper {
        display: flex;
        flex-direction: column;

        @media (min-width: 1024px) {
            flex-direction: row;
        }
    }

    article.order-summary-wrapper,
    article.cart-summary-wrapper {
        padding: 10px 10px 15px 10px;
        border-bottom: 1px solid #d6deeb;
        position: relative;

        @media (min-width: 1024px) {
            border-bottom: 0;
            order: 2;
        }
    }

    ul.summary-box {
        width: 100%;
        margin-bottom: 10px;
        border: 1px solid #87b4e9;
        font-size: 14px;

        @media (min-width: 1024px) {
            width: 300px;
        }

        li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 15px 20px 0 20px;

            .name {
                font-weight: 500;
                color: #738096;
            }

            p {
                font-weight: 500;
                color: #40474d;
            }

            span {
                margin-left: 3px;
            }

            &.total-bundle {
                padding: 20px;
                margin-top: 15px;
                background-color: #ebf6ff;

                p.total-price {
                    font-size: 20px;
                    font-weight: 500;
                    color: #1f7ce6;
                }
            }
        }
    }

    article.order-info-container,
    article.cart-list-wrapper {
        flex: 1;

        @media (min-width: 1024px) {
            order: 1;
        }

        button.input-button {
            background-color: white;
            color: #666666;
            border: 1px solid #d1d5d5;
            box-shadow: 1px 1px 0 rgba(209, 219, 217, 0.3);
            width: 59px;
            height: 29px;
            font-size: 12px;
        }

        .head {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 10px;

            label.checkbox--component {
                span {
                    color: #303538;
                    font-weight: 500;
                    font-size: 13px;
                    padding-left: 10px;
                }
            }
        }

        ul.list-box {
            padding: 0 10px;

            li {
                display: flex;
                padding: 20px 0;
                border-bottom: 1px solid #d6deeb;
            }
        }

        .checkbox-wrap {
            display: flex;
            align-items: center;
        }

        figure.image-wrap {
            padding-left: 10px;

            img {
                width: 100px;
                height: 100px;
                object-fit: cover;
                border-radius: 4px;
            }
        }

        .info-wrap {
            flex: 1;
            font-size: 13px;
            padding-left: 15px;
        }

        .product-bundle {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @media (min-width: 768px) {
                flex-direction: row;
                align-items: center;
            }
        }

        .product-name {
            font-weight: 500;
        }

        .product-price {
            .name {
                font-size: 12.5px;
                color: #666;
                margin-bottom: 6px;

                @media (min-width: 768px) {
                    margin-bottom: 8px;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }

            p {
                display: inline-block;
                font-size: 13px;
                font-weight: 500;
                color: #1f7ce6;
                padding-left: 5px;
            }
        }

        .product-count {
            @media (min-width: 768px) {
                padding-right: 35px;
            }
        }

        .button-wrap {
            display: flex;
            align-items: center;
        }
    }
}
