label.checkbox--component {
    display: flex;
    align-items: center;

    * {
        cursor: pointer;
    }

    input {
        appearance: none;
        width: 16px;
        height: 16px;
        // margin-bottom: -1px;
        border: 0;
        box-sizing: border-box;

        // @media (min-width: 768px) {
        //     width: 13px;
        //     height: 13px;
        // }

        &:after {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            margin-bottom: -2px;
            border: 1px solid rgba(214, 214, 214, 0.8);
            border-radius: 4px;
            background-color: white;
            transition: 0.3s;
            box-sizing: border-box;

            // @media (min-width: 768px) {
            //     width: 13px;
            //     height: 13px;
            // }
        }

        &:hover {
            &:after {
                border: 1px solid rgba(214, 214, 214, 1);
            }
        }

        &[type='checkbox']:checked {
            &:after {
                background-color: white;
                background-image: url('/assets/images/icons/check.svg');
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }

    span {
        color: #738096;
        font-size: 12px;
        padding-left: 0.3rem;

        &:hover {
            color: #1f8ce6;
        }
    }
}
