article#cosmetics,
article#health,
article#new-products {
    background-image: url('https://books.ridicdn.net/static/image/recommended_book_background@desktop.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding: 36px 0 50px;

    .container {
        margin: 0 auto;

        @media (min-width: 1024px) {
            max-width: 1000px;
        }
    }

    h2 {
        padding-left: 16px;
        margin-bottom: 30px;
        font-size: 19px;
        line-height: 19px;
        color: white;

        @media (min-width: 1024px) {
            max-width: 1000px;
            padding-left: 24px;
            margin: 0 auto 30px auto;
        }
    }

    ul.mobile {
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        padding-left: 16px;
        padding-right: 16px;
        margin-top: 6px;

        @media (min-width: 1024px) {
            display: none;
        }

        li {
            margin-right: 12px;

            &:last-child {
                margin-right: 12px;
                padding-right: 12px;
            }
        }

        img {
            width: 132px;
            height: 132px;
            object-fit: cover;
            margin-bottom: 8px;
            border-radius: 6px;
        }

        figcaption {
            font-size: 14px;
            line-height: 1.5;
            color: white;
        }

        b {
            width: 132px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: 7px;
            font-weight: bold;
        }

        .name {
            font-size: 0.9em;
            opacity: 0.9;
        }

        p {
            opacity: 0.7;
            display: inline-block;
            padding-left: 10px;
        }

        span {
            margin-left: 3px;
        }
    }

    .desktop {
        display: none;
        position: relative;
        margin: 0 9.5px;
        padding-left: 36px;

        @media (min-width: 1024px) {
            display: block;
        }

        .swiper-slide {
            width: 20%;
        }

        img {
            width: 160px;
            height: 160px;
            object-fit: cover;
            margin-bottom: 8px;
            border-radius: 6px;
        }

        figcaption {
            font-size: 14px;
            line-height: 1.5;
            color: white;
        }

        b {
            width: 132px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-weight: bold;
            margin-bottom: 7px;

            @media (min-width: 768px) {
                font-size: 15px;
            }
        }

        .name {
            font-size: 0.9em;
            opacity: 0.9;
        }

        p {
            opacity: 0.7;
            display: inline-block;
            padding-left: 10px;
        }

        span {
            margin-left: 3px;
        }

        button {
            position: absolute;
            z-index: 2;
            top: 40%;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: rgba(56, 66, 82, 1);
            box-shadow: rgba(0, 0, 0, 0.33) 0px 0.8px 3px;
            // opacity: 0.5;
            transition: 0.2;

            &::before {
                content: '';
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 50%;
                width: 10px;
                height: 10px;
                border-top: 1px solid rgb(184, 191, 196);
                border-left: 1px solid rgb(184, 191, 196);
            }

            &.newproducts-button-prev,
            &.health-button-prev,
            &.cosmetics-button-prev {
                left: 0;
                transform: translate(0, -50%);

                &::before {
                    transform: translate(-30%, -50%) rotate(-45deg);
                }
            }

            &.newproducts-button-next,
            &.health-button-next,
            &.cosmetics-button-next {
                right: 0;
                transform: translate(10%, -50%);

                &::before {
                    transform: translate(-70%, -50%) rotate(135deg);
                }
            }
        }
    }
}

article#cosmetics,
article#health {
    &.category-swiper {
        background: initial;
        padding: 20px 0 20px;

        @media (min-width: 768px) {
            padding: 36px 0 30px;
        }

        h2 {
            color: black;

            a {
                &::after {
                    content: '〉';
                    display: inline-block;
                    padding-left: 8px;
                    transform: scale(0.85);
                }
            }
        }

        figcaption > * {
            color: black;
        }

        .name {
            opacity: 0.7;
        }

        p {
            opacity: 0.7;
        }

        button {
            background-color: white !important;
            box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.33) !important;
        }
    }
}
