article#main-banner {
    position: relative;
    padding: 15px 0;

    .swiper-container {
        position: relative;
    }

    .swiper-slide {
        min-width: 280px;
        width: calc(100vw - 20px);
        height: calc((100vw -23px) / 1.5);

        @media (min-width: 375px) {
            width: 355px;
            height: 255px;
        }

        @media (min-width: 768px) {
            width: 450px;
            height: calc(450px / 1.5);
        }

        .wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 96%;
                height: 96%;
                object-fit: cover;
                border-radius: 6px;

                @media (min-width: 375px) {
                    max-width: 355px;
                    max-height: 255px;
                }

                @media (min-width: 768px) {
                    max-width: 430px;
                    max-height: calc(430px / 1.5);
                }
            }
        }
    }

    .swiper-pagination {
        width: 54px;
        height: 24px;
        border-radius: 12px;
        border: 1px solid rgba(255, 255, 255, 0.2);
        background-color: rgba(0, 0, 0, 0.4);
        left: unset;
        right: 18px;
        bottom: 15px;
        color: white;
        font-size: 12px;
        padding: 3px 5px 0 5px;

        @media (min-width: 768px) {
            right: 50%;

            transform: translate(50%, 0);
        }
    }

    .carousel-button-prev,
    .carousel-button-next {
        @media (min-width: 768px) {
            position: absolute;
            z-index: 2;
            top: 50%;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: white;
            opacity: 0.5;
            transition: 0.2s;

            &::before {
                content: '';
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 50%;
                width: 10px;
                height: 10px;
                border-top: 1px solid black;
                border-left: 1px solid black;
            }

            &:hover {
                opacity: 1;
            }
        }
    }
    .carousel-button-prev {
        left: 50%;
        transform: translate(-280px, -50%);

        &::before {
            transform: translate(-30%, -50%) rotate(-45deg);
        }
    }
    .carousel-button-next {
        right: 50%;
        transform: translate(280px, -50%);

        &::before {
            transform: translate(-70%, -50%) rotate(135deg);
        }
    }
}
