article#scroll-move {
    padding: 1px 16px 24px;

    @media (min-width: 768px) {
        padding: 5px 16px 24px;
    }

    .container {
        max-width: 1000px;
        margin: 0 auto;
    }

    ul {
        display: flex;
        justify-content: space-around;
    }

    li {
        &:first-child {
            svg {
                fill: #b9bef1;
            }
        }
        &:nth-child(2) {
            svg {
                fill: #e8cb99;
            }
        }
        &:nth-child(3) {
            svg {
                fill: #99d1cb;
            }
        }
        &:nth-child(4) {
            svg {
                fill: #f0b9b9;
            }
        }
        &:last-child {
            svg {
                fill: #f1c0a7;
            }
        }
    }

    svg {
        width: 44px;
        height: 44px;
    }

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .relative {
        margin-bottom: 8px;
    }

    span {
        display: inline-block;
        min-width: 44px;
        font-size: 13px;
        line-height: 1.23;
        color: #525a61;
        text-align: center;
        word-break: keep-all;
    }
}
