section#order-complete,
section#order {
    .container {
        max-width: 1000px;
        margin: 0 auto;

        @media (min-width: 1024px) {
            padding: 24px 13px;
        }
    }

    h3.order_article_title {
        font-size: 15px;
        font-weight: 500;
        color: rgb(65, 71, 75);
        padding-bottom: 9px;

        &.bottom-border {
            border-bottom: 1px solid rgb(129, 138, 146);
        }
    }

    .input-group {
        .input-wrapper .text-input {
            height: 36px;
            font-size: 13px;
            padding: 17px 9px 7px;
        }

        .placeholder {
            top: 12px;
            font-size: 13px;

            &.has-value {
                transform: translateY(-9px);
                font-size: 9px;
                color: #1f8ce6;
            }
        }

        .input-wrapper .text-input:focus + .placeholder {
            transform: translateY(-9px);
            font-size: 9px;
            color: #1f8ce6;
        }
    }
    .select-group {
        height: 36px;
        font-size: 13px;
    }

    article.order-info-container {
        // margin-bottom: 24px;

        .order_list_wrapper,
        .order_delivery_wrapper {
            margin-bottom: 50px;
        }

        ul.list-box {
            padding: 0;

            span.count {
                &::before {
                    content: '수량';
                    padding-right: 5px;
                }
            }
        }
    }

    .order_complete_wrapper,
    .order_delivery_wrapper,
    .order_list_wrapper {
        padding: 0 10px 0;
    }

    .order_delivery_wrapper {
        font-size: 12px;

        .contents-wrapper {
            padding: 0 10px;
        }

        .radio-group {
            padding: 10px 0 20px;

            span {
                padding-left: 7px;
            }

            label {
                margin-right: 12px;
            }

            input {
                cursor: pointer;
            }
        }

        .existing-addr-box {
            padding-top: 5px;
            padding-bottom: 13px;
            display: none;

            p {
                font-size: 13px;
                padding-top: 13px;

                &:first-child {
                    padding-top: 0;
                }
            }

            &.active {
                display: block;
            }
        }

        .new-addr-box {
            padding-top: 5px;
            display: none;

            &.active {
                display: block;
            }
        }
    }

    .order_complete_wrapper {
        ul.summary-box {
            font-size: 13px;

            @media (min-width: 1024px) {
                width: auto;
            }
        }
    }

    .order_complete_buttons_wrapper {
        display: flex;
        justify-content: center;
        padding: 24px 10px;

        button {
            max-width: 300px;

            &:first-child {
                margin-right: 10px;
                background-color: white;
                color: rgb(31, 140, 230);
                border: 1px solid rgb(31, 140, 230);
            }
        }
    }
}
