a.top--button {
    position: fixed;
    right: 6%;
    bottom: 6%;
    z-index: 999;
    display: block;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
    transition: 0.3s;
    visibility: hidden;

    @media (min-width: 768px) {
        width: 55px;
        height: 55px;
    }

    &:hover {
        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
        background-color: rgba(255, 255, 255, 0.9);
    }

    &::before,
    &::after {
        content: '';
        display: inline-block;
        width: 13px;
        height: 13px;
        border: {
            top: 1px solid rgba(102, 102, 102, 0.5);
            left: 1px solid rgba(102, 102, 102, 0.5);
        }
        position: absolute;
        top: 50%;
        left: 50%;
    }

    &::before {
        transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
        transform: translate(-50%, 0%) rotate(45deg);
    }
}
