@mixin loopFunc($className, $styleName, $min, $max, $unit, $direction: null) {
    @if $direction == 'vertical' {
        @for $i from $min through $max {
            #{$className + $i} {
                #{$styleName}: #{$i + $unit} 0 !important;
            }
        }
    } @else if $direction == 'horizontal' {
        @for $i from $min through $max {
            #{$className + $i} {
                #{$styleName}: 0 #{$i + $unit} !important;
            }
        }
    } @else {
        @for $i from $min through $max {
            #{$className + $i} {
                #{$styleName}: #{$i + $unit} !important;
            }
        }
    }
}

@mixin decimalLoopFunc($className, $styleName, $min, $max, $unit, $direction: null) {
    $decimal: '.';

    @if $direction == 'vertical' {
        @for $i from $min through $max {
            #{$className + $i} {
                #{$styleName}: #{$decimal}#{$i + $unit} 0 !important;
            }
        }
    } @else if $direction == 'horizontal' {
        @for $i from $min through $max {
            #{$className + $i} {
                #{$styleName}: 0 #{$decimal}#{$i + $unit} !important;
            }
        }
    } @else {
        @for $i from $min through $max {
            #{$className + $i} {
                #{$styleName}: #{$decimal}#{$i + $unit} !important;
            }
        }
    }
}

@mixin loopFuncCalculation(
    $className,
    $styleName,
    $min,
    $max,
    $offset,
    $unit: null,
    $arithmetic: null
) {
    @if $arithmetic == 'divide' {
        @for $i from $min through $max {
            #{$className + $i * $offset} {
                #{$styleName}: #{$i / $offset + $unit} !important;
            }
        }
    }
    @if $arithmetic == 'multiply' {
        @for $i from $min through $max {
            #{$className + $i * $offset} {
                #{$styleName}: #{$i * $offset + $unit} !important;
            }
        }
    }
}

/* opacity */
@include loopFuncCalculation('.opacity-', 'opacity', 0, 10, 10, '', 'divide');

/* width */
@include loopFuncCalculation('.w-', 'width', 1, 10, 10, '%', 'multiply');
.w-max-initial {
    max-width: none !important;
}

/* height */
@include loopFuncCalculation('.h-', 'height', 1, 10, 10, '%');

/* margin */
.m-auto {
    margin: 0 auto;
}
@include loopFunc('.m-', 'margin', 0, 5, 'rem');
@include loopFunc('.mt-', 'margin-top', 0, 5, 'rem');
@include loopFunc('.ml-', 'margin-left', 0, 5, 'rem');
@include loopFunc('.mr-', 'margin-right', 0, 5, 'rem');
@include loopFunc('.mb-', 'margin-bottom', 0, 5, 'rem');
@include loopFunc('.my-', 'margin', 0, 5, 'rem', 'vertical');
@include loopFunc('.mx-', 'margin', 0, 5, 'rem', 'horizontal');
@include decimalLoopFunc('.m-0', 'margin', 1, 9, 'rem');
@include decimalLoopFunc('.mt-0', 'margin-top', 1, 9, 'rem');
@include decimalLoopFunc('.mr-0', 'margin-right', 1, 9, 'rem');
@include decimalLoopFunc('.mb-0', 'margin-bottom', 1, 9, 'rem');
@include decimalLoopFunc('.ml-0', 'margin-left', 1, 9, 'rem');
@include decimalLoopFunc('.my-0', 'margin', 1, 9, 'rem', 'vertical');
@include decimalLoopFunc('.mx-0', 'margin', 1, 9, 'rem', 'horizontal');

/* padding */
@include loopFunc('.p-', 'padding', 0, 5, 'rem');
@include loopFunc('.pt-', 'padding-top', 0, 5, 'rem');
@include loopFunc('.pl-', 'padding-left', 0, 5, 'rem');
@include loopFunc('.pr-', 'padding-right', 0, 5, 'rem');
@include loopFunc('.pb-', 'padding-bottom', 0, 5, 'rem');
@include loopFunc('.py-', 'padding', 0, 5, 'rem', 'vertical');
@include loopFunc('.px-', 'padding', 0, 5, 'rem', 'horizontal');
@include decimalLoopFunc('.p-0', 'padding', 1, 9, 'rem');
@include decimalLoopFunc('.pt-0', 'padding-top', 1, 9, 'rem');
@include decimalLoopFunc('.pr-0', 'padding-right', 1, 9, 'rem');
@include decimalLoopFunc('.pb-0', 'padding-bottom', 1, 9, 'rem');
@include decimalLoopFunc('.pl-0', 'padding-left', 1, 9, 'rem');
@include decimalLoopFunc('.py-0', 'padding', 1, 9, 'rem', 'vertical');
@include decimalLoopFunc('.px-0', 'padding', 1, 9, 'rem', 'horizontal');

/* color */
.color-white {
    color: white;
}
.color-black {
    color: black;
}

/* font weight */
.font-light {
    font-weight: lighter !important;
}
.font-bold {
    font-weight: bold !important;
}

/* text align */
.text-center {
    text-align: center !important;
}

/* word */
.white-space-nowrap {
    white-space: nowrap;
}

/* flex */
.flex {
    display: flex !important;

    &.column {
        flex-direction: column !important;
    }
    &.row {
        flex-direction: row !important;
    }
    &.flex-wrap {
        flex-wrap: wrap !important;
    }
}
.direction-column {
    flex-direction: column !important;
}
.flex-wrap {
    flex-wrap: wrap !important;
}
.justify-start {
    justify-content: flex-start !important;
}
.justify-center {
    justify-content: center !important;
}
.justify-end {
    justify-content: flex-end !important;
}
.justify-around {
    justify-content: space-around !important;
}
.justify-between {
    justify-content: space-between !important;
}
.align-start {
    align-items: flex-start !important;
}
.align-center {
    align-items: center !important;
}
.align-end {
    align-items: flex-end !important;
}
.align-around {
    align-items: space-around !important;
}
.align-between {
    align-items: space-between !important;
}
.flex-1 {
    flex: 1;
}

/* position */
.absolute {
    position: absolute;
}
.relative {
    position: relative;
}
.fixed {
    position: fixed;
}
.centered-x {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}
.centered-y {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

/* display */
.block {
    display: block;
}
.inline-block {
    display: inline-block;
}
.inline {
    display: inline;
}
.none {
    display: none;
}

/* visibility */
.visible {
    visibility: visible;
}
.hidden {
    visibility: hidden;
}

/* border */
.border-0 {
    border: 0 !important;
}

/* 
    form
*/
/* input */
.input-group {
    margin-bottom: 10px;
}

.input-wrapper {
    display: block;
    position: relative;
    width: 100%;
    margin-bottom: -1px;
}

.input-group .input-wrapper:last-child > .input {
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 4px;
}

.input-group .input-wrapper:first-child > .input {
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
}

.input-wrapper .text-input {
    letter-spacing: -0.03em;
    width: 100%;
    height: 48px;
    padding: 21px 10px 7px;
    position: relative;
    display: block;
    font-size: 15px;
    line-height: 0;
    box-shadow: none;
    overflow: hidden;
    background-color: white;
    border: 1px solid #d6deeb;
    transition: 0.2s;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    z-index: 1;
}

.placeholder {
    position: absolute;
    color: #666;
    letter-spacing: -0.03em;
    line-height: 1em;
    top: 18px;
    left: 10px;
    cursor: auto;
    font-size: 15px;
    transition: 0.2s;
    pointer-events: none;
    transition: 0.2s;
    z-index: 3;

    &.has-value {
        transform: translateY(-12px);
        font-size: 11px;
        color: #1f8ce6;
    }
}
.input-wrapper .text-input:focus {
    border: 1px solid #1f8ce6;
    z-index: 2;
}
.input-wrapper .text-input:focus + .placeholder {
    transform: translateY(-12px);
    font-size: 11px;
    color: #1f8ce6;
}

.button-check-group {
    display: flex;

    & > .input-group {
        flex: 1;
    }

    & > .input-button {
        min-width: 70px;
        max-width: 100px;
        margin-left: 10px;
    }
}

/* textarea */
.resize-none {
    resize: none;
}

/* label */
label {
    cursor: pointer;
}

/* select */
select.select-group {
    display: block;
    width: 100%;
    height: 48px;
    font-size: 15px;
    margin-bottom: 10px;
    padding-left: 10px;
    color: #666;
    border: 1px solid #d6deeb;
    background-color: white;
    background-image: url('https://icongr.am/feather/chevron-down.svg?size=18&color=666666');
    background-repeat: no-repeat;
    background-position: 96% center;
}

/* button */
button.input-button {
    display: block;
    width: 100%;
    height: 48px;
    font-size: 15px;
    color: white;
    background-color: #1f8ce6;
    border-radius: 4px;
}
