section#notification-detail {
    h2.module_pagetitle {
        display: block;
    }

    article.noti_detail_wrapper {
        font-size: 14px;
        color: #333;
        padding-bottom: 30px;

        @media (min-width: 768px) {
            padding: 0 22px;
        }
    }

    h3.title_wrapper {
        font-size: 14px;
        padding: 14px 14px 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
        line-height: 1.4em;

        @media (min-width: 768px) {
            font-size: 15px;
        }

        p {
            strong {
                margin-right: 7px;
            }
        }

        time {
            font-size: 12px;
            opacity: 0.6;
            display: inline-block;
            padding-top: 5px;
            padding-left: 5px;
        }
    }

    .contents-box {
        line-height: 1.5em;
        font-size: 14px;
        padding: 14px;

        img {
            display: block;
            width: 100%;
            margin: 10px auto;

            @media (min-width: 768px) {
                max-width: 500px;
            }
        }

        .attach-wrapper {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            margin-top: -7px;
            margin-bottom: 10px;
            font-size: 12px;
            color: #888;

            li {
                height: 28px;
                display: flex;
                align-items: center;

                @media (min-width: 768px) {
                    max-width: 200px;
                }

                a {
                    display: flex;
                    align-items: center;

                    .name {
                        margin-right: 6px;
                    }
                }
            }
        }
    }

    .bottom-wrapper {
        border-top: 1px solid rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: flex-end;
        padding-top: 14px;
        padding-right: 14px;

        button {
            width: 70px;
            height: 32px;
            color: #1f8ce5;
            border: 1px solid #1f8ce5;
            background-color: white;
            transition: 0.2s;

            &:hover {
                box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
            }
        }
    }
}
