footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: #303538;
    margin-top: 20px;

    .container {
        max-width: 1000px;
        max-height: 700px;
        margin: 0 auto;
        padding: 24px 16px;
    }

    .email {
        font-size: 14px;
        color: white;
        margin-bottom: 35px;
    }

    address {
        font-size: 11px;
        line-height: 1.82;
        color: #7e8992;
        margin-bottom: 16px;
    }

    .copyright {
        font-size: 14px;
        font-weight: 500;
        color: #7e8992;
    }
}
