section#notification-detail,
section#notification {
    .container {
        width: 100%;
        max-width: 952px;
        margin: 0 auto;

        @media (min-width: 768px) {
            margin-top: 30px;
            padding-bottom: 70px;
        }
    }

    h2.module_pagetitle {
        display: none;

        @media (min-width: 768px) {
            display: block;
        }
    }

    article.noti_list_wrapper {
        font-size: 14px;
        color: #666;
        display: block;
        margin: 0;
        padding: 0;

        @media (min-width: 768px) {
            padding: 0 16px;
        }

        ul {
            li {
                position: relative;
                border-bottom: 1px solid #e5e5e5;
                width: 100%;

                .list_table {
                    display: table;
                    width: 100%;

                    .list_left,
                    .list_right {
                        display: table-cell;
                        vertical-align: middle;
                        padding: 14px 0;
                    }

                    .list_left {
                        width: 70px;
                        text-align: center;
                        padding-left: 10px;

                        .noti_icon {
                            &.icon_book {
                                box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);

                                img {
                                    width: 80px;
                                    height: 80px;
                                    object-fit: cover;
                                    border: 1px solid #bbb;

                                    @media (min-width: 1024px) {
                                        width: 100px;
                                        height: 100px;
                                    }
                                }
                            }
                        }

                        .icon_book {
                            display: inline-block;
                        }
                    }

                    .list_right {
                        padding-right: 34px;
                        padding-left: 13px;

                        @media (min-width: 768px) {
                            padding-right: 60px;
                            padding-left: 22px;
                        }

                        .noti_title {
                            p {
                                color: #000;
                                line-height: 1.4em;
                                margin-bottom: 5px;
                                letter-spacing: -0.03em;
                                transition: all 0.1s;

                                strong {
                                    margin-right: 7px;
                                }
                            }
                        }

                        .noti_time {
                            font-size: 12px;
                        }
                    }
                }

                a.btn_more {
                    display: block;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                    background-image: url('https://icongr.am/feather/chevron-right.svg?size=18&color=bbbbbb');
                    background-repeat: no-repeat;
                    background-position: 97% center;

                    @media (min-width: 768px) {
                        background: unset;
                    }
                }
            }
        }
    }
}
